body {
    margin: 0;
    padding: 0;
    font-family: 'Poppins', sans-serif;
    background-color: #fcfcfc;
  }

  .navbar{
    margin: 0px ;
    padding: 0px;
  }

  .container {
    max-width: 100%;
    /* margin: 0 auto; */
    padding: 0 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 100%;
  }
  .company{
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .company img{
    height: 70px;
    width: auto;
    margin: auto 0;
  }
  .company h4{
      color: rgb(235, 235, 235); 
      margin: auto 0;
      display: flex;
    align-items: center;
    justify-content: center;
      /* font-family: "Inter Placeholder", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif; */
      font-family: "sans-serif", serif;
      font-weight: 700;
      }
  
  .navbar {
    height: 70px;
    /* background-color: #28162D; */
    box-shadow: rgba(6, 26, 54, 0.06) 0px 4px 12px;  
    background: #28162D;
  /* background: linear-gradient(90deg, rgb(106, 10, 26) 11%, rgb(36, 20, 48) 71%); */
  /* background: linear-gradient(90deg, #643875 11%, #8e2446 71%); */
  /* background: linear-gradient(#723A89 0%, #AD1C49 100%); */


    position: relative;
    box-shadow: 0px 6px 4px rgba(0, 0, 0, 0.2);
  }
  
  
  .nav-elements ul {
    display: flex;
    justify-content: space-between;
    align-items: center;
    list-style-type: none;
    margin: 0px;
  }

  .nav-elements ul li:not(:last-child) {
    margin-right: 60px;
  }
  
  .nav-elements ul a {
    font-size: 16px;
    font-weight: 400;
    color: rgb(239, 239, 239) !important;
    text-decoration: none;
    /* font-family: "Inter Placeholder", -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif; */
    font-family: "Fira Sans", sans-serif;
  font-style: normal;
  font-weight: 450;
  font-size: 100%;
  letter-spacing: 0.05em;
    }

  .nav-elements ul a.active {
    color: #574c4c;
    font-weight: 500;
    position: relative;
  }
  
  .nav-elements ul a.active::after {
    content: '';
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 100%;
    height: 2px;
    background-color: #574c4c;
  }