    margin-left: 300px;
    /* border: 1px solid black; */
    box-shadow: rgba(0, 0, 0, 0.06) 0px 4px 12px; ;
    border-radius: 10px;
    background-color: white;
    /* margin-bottom: 50px; */
}
.about-main{
    margin-top: 50px;
    justify-content: center;
    display: flex;
    /* margin-bottom: 30px; */
    font-family: "Playfair Display", serif;
  font-optical-sizing: auto;
