body{
    background-color: #F8F8F8;
}
.main-gs{
    position: relative;
    margin-left: 150px;
    margin-right: 150px;
    box-shadow: rgba(6, 26, 54, 0.06) 0px 4px 12px;  
    display: flex;
    justify-content: space-between;
    padding: 100px;
    padding-top: 100px;
    background-color: white;
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;

}
.gs-left h2{
    font-weight: 700;
    color: black !important;
    font-size: 200%;
}
.gs-left h1{
    font-weight: 800;
    margin: 70px 0px;
    line-height: 0.000000009;
}
.gs-left p{
    font-weight: 500;
    color: rgb(39, 39, 39);
}
.gs-left li{
    font-weight: 600;
    line-height: 2;
}
li::marker{
    color: #910D22;
}
.image1{
    
    width: 560px;
    height: auto;
    position: absolute;
    bottom: 0;
    right: 0;
    border-bottom-right-radius: 10px;

}
.gs-right{
    /* background-color: rgb(242, 242, 242); */
    padding: 32px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.06) 0px 4px 12px; ;
    align-items: center;
}
.gs-right p{
    font-size: 90%;
    font-weight: 500;
}
.gs-right input{
    background-color: #F2F2F2 !important;
    border: none;
    border-bottom: 1px solid rgba(49, 49, 49, 0.901);
    color: rgba(0, 0, 0, 0.779);
    height: 50px;

}

input:focus{
    outline: none !important;
    box-shadow: none !important;
}
label{
    color: rgba(75, 75, 75, 0.76) ;
}

.reg-but{
    padding:0px 40px ;
    justify-content: center;
    margin-top: 50px;
    align-items: center;   
}

.primarygs{
    width: 300px;
    background: linear-gradient(90deg, #6F33C6 11%,  #C51234 71%);
    height: 40px;
    border-radius: 10px;
    font-weight: 600;
    font-size: 120%;
    color: white;
    border: 0px;
}