.outer-support{
    padding: 50px;
    height: 78vh;
    padding-top: 20px;
    margin-right: 50px;
    margin-top: 40px;
    margin-left: 300px;
    /* border: 1px solid black; */
    box-shadow: rgba(0, 0, 0, 0.06) 0px 4px 12px; ;
    border-radius: 10px;
    background-color: white;
    /* margin-bottom: 50px; */
}
.support-main{
    margin-top: 50px;
    justify-content: center;
    display: flex;
    /* margin-bottom: 30px; */
    font-family: "Playfair Display", serif;
  font-optical-sizing: auto;
  font-weight: 600;
  font-style: normal;
}
.outer-lower{
    margin-top: 50px;
    margin-bottom: 80px;
    display: flex;
    justify-content: center;
}
.outer-lower h4{
    margin-top: 40px;
    color: black !important;
}
.outer-lower p{
    color: black !important;
}
.div-1{
    margin-left:20px;
    margin-right: 20px; 
    box-shadow: rgba(0, 4, 9, 0.06) 5px 5px 12px;  
    padding: 20px;
    z-index: 1;
    transition: background-color 0.3s;

}
.div-1:hover{
    background-color: rgb(242, 241, 241);
}
