:root {
    --navBg: #28162D;
  }
  .nav-menu {
    background-color: var(--navBg);
    width: 250px;
    height: 100vh;
    display: flex;
    justify-content: center;
    position: fixed;
    top: 0;
    left: -100%;
    transition: 850ms;
    /* z-index: 1; */
  }
  .sidenavbar {
    background-color: var(--navBg);
    height: 80px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    /* z-index: 1; */
  }
  .usernav{
    /* margin-left: 20px; */
    margin-right: 60px;
  }
  .menu-bars {
    margin-left: 2rem;
    font-size: 2rem;
    background: none;
  }
  
 
  
  .nav-menu.active {
    left: 0;
    transition: 350ms;
  }
  
  .nav-text {
    display: flex;
    justify-content: start;
    align-items: center;
    padding: 8px 0 8px 16px;
    list-style: none;
    height: 60px;
  }
  
  .nav-text a {
    text-decoration: none;
    color: #f5f5f5;
    font-size: 18px;
    width: 95%;
    height: 100%;
    display: flex;
    align-items: center;
    padding: 0 16px;
    border-radius: 4px;
  }
  
  .logout-but:hover {
    background-color: #C8102E;
  }
  
  .nav-text a:hover {
    background-color: #C8102E;
  }
  
  .nav-menu-items {
    width: 100%;
    padding: 0;
  }
  
  .navbar-toggle {
    background-color: var(--navBg);
    width: 100%;
    height: 80px;
    display: flex;
    justify-content: start;
    align-items: center;
  }
  
  span {
    margin-left: 16px;
  }
  .userhome{
    width: 60px;
    height: 40px;
    background-color: #28162D;
    /* border-radius: 5px; */
    border: 0px;
    color: white;
    /* transition: background-color 0.3s; */
  }
