.layout{
    margin: 0 100px;

}
.header{
    background-color: #0e1010;
    /* background: linear-gradient(90deg, rgba(1,195,204,1) 20%, rgba(125,42,232,1) 90%); */
    padding: 20px;
    border-bottom-right-radius: 25px;
    border-bottom-left-radius: 25px;

}
.logo{
    font-size: 30px;;
    color: aliceblue;
    margin: 0 !important;
    cursor: pointer;
}
.username{
    font-size: 18px;
    color: aliceblue;
}
.content{
    height: 80vh;
    background-color: white;
    border: 0px;
    /* box-shadow: 0 0 5px rgb(72, 72, 72); */
    box-shadow: rgba(6, 20, 39, 0.06) 0px 4px 12px; 
    margin-left: 200px;
    margin-bottom: 30px;
    margin-top: 40px;
    border-radius: 10px;
    padding: 15px;
    margin-right: -50px;
    padding: 50px;
    height: 78vh;
    padding-top: 20px;
    overflow-y: scroll;
}
.content::-webkit-scrollbar {
    display: none;
}

@media screen and (max-width:700px){
    .layout{
        margin: 0 15px;
    }
}

.spinner{
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.ant-spin-dot-item{
    background-color: black !important;
}
.addnew{
    align-items: center;
    align-content: center;
    justify-content: center;
    display: flex;
    background: #C8102E;
    color: white;
    /* border-width: 1px; */
    /* border-style: solid; */
    /* border-image: initial; */
    border: 0px;
    height: 40px;
    border-radius: 8px;
    padding: 16px 20px;
    font-size: 18px;
    font-weight: 500;
    transition: background-color 0.3s;

}
.addnew:hover {
    background: #7d0c1e;
  }
  