.main-contents{
    margin: 100px;
    display: flex;
    align-content: end;
    position: relative;

}
.main-contents h1{
    font-size: 400%;
    font-weight: 800;
    color: black;
    font-family: "Rocket Sans Bold", sans-serif;
}
.main-para{
    margin-top: 30px;
}
.main-line{
    padding: 30px;
}
.main-line p{
    font-size: 22px;
    letter-spacing: 0em;
    line-height: 1;
    font-weight: 400;
}
