element.style {
    opacity: 1;
    transform: translateX(-50%) translateX(0px) translateY(2.50059px) scale(1) rotate(0deg) rotateX(0deg) rotateY(0deg) skewX(0deg) skewY(0deg) translateZ(0px) rotate(8deg);
}
element.style {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: inherit;
    object-position: center center;
    object-fit: cover;
    image-rendering: auto;
}
img {
    overflow-clip-margin: content-box;
    overflow: clip;
}
.main-mid{
    /* background-color: #28162D; */
    padding: 100px;
    padding-left: 150px;
    background: linear-gradient(#723A89 0%, #AD1C49 100%);

}
.main-mid h1{
    color: white;
    font-weight: 700;

}
.mid-t{
    margin-top: 50px;
    
}
.mid-t p{
    color: white !important;
}

.floating-image {
    width: 330px;
    height: auto;
    position: absolute;
    top: 7%;
    left: 65%;
    transform: rotate(10deg) translateY(0); /* Initial position */
    z-index: 1;
    animation: float 4s ease-in-out infinite;
  }
  
  @keyframes float {
    0% {
      transform: rotate(10deg) translateY(0);
    }
    50% {
      transform: rotate(10deg) translateY(-30px); /* Translate up */
    }
    100% {
      transform: rotate(10deg) translateY(0); /* Back to initial position */
    }
  }
  .main-line button {
    background: #9b1229;
    color: white;
    border-width: 1px;
    border-style: solid;
    border-image: initial;
    height: 56px;
    border-radius: 8px;
    padding: 16px 20px;
    font-size: 18px;
    font-weight: 500;
    transition: background-color 0.3s;
  }
  
  .main-line button:hover {
    background: #7d0c1e;
  }
  
  .FaqEdu {
    max-width: 1000px;
    margin: 0 auto;
    position: relative;
    top:-10rem;
   
  }
  
  
  .FaqEdu h1 {
