.filter{
    box-shadow: 0 0 3px gray;
    padding: 15px 20px;
    border-radius: 15px;
}
.transaction-form label{
    color: black !important;

}
.view-switch{
    border: 1px solid black;
    border-radius: 3px;
    padding: 5px 10px;
}
.anticon svg{
    font-size: 20px;
    cursor: pointer;
}
.active-icon{
    color: black;
}
.inactive-icon{
    color: gray;
}
@media screen and(max-width: 600px){
    .filter{
        overflow-x: scroll;
    }
}

